html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-rubik);
  background: var(--bg-body);
  color: var(--white);
  min-height: 150vh;
  background-image: url("../../../image/desktop_BG.svg");
  background-repeat: no-repeat;
  background-position: rem(-10);
  background-size: 100%;
  background-attachment: local;
}

:root {
  --bg-body: rgba(29, 30, 30, 1);
  --main-yellow: rgba(247, 247, 36, 1);
  --white: rgba(255, 255, 255, 1);
  --bg-section: rgba(61, 61, 61, 0.28);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.yellow {
  color: var(--main-yellow);
}

.hero_main-link {
  font-weight: bold;
  font-size: 3rem;
  margin-left: 2.5rem;
  color: var(--main-yellow);
}

.hero_sub-link {
  font-weight: bold;
  font-size: 1.875rem;
  color: var(--white);
}

.hero-main-h1 {
  width: 59.375rem;
  font-size: 9.6875rem;
}

.section_Header-one {
  margin-top: 25rem;
  margin-bottom: 9.375rem;
  font-size: 4.5rem;
}

.text {
  display: inline-block;
  font-size: 1.75rem;
  width: 83rem;
  margin-top: 0;
  font-weight: 400;
}

.last_h1 {
  display: inline-block;
  width: 62.5rem;
  font-size: 8rem;
  margin-left: 9.375rem;
}

.footer {
  color: var(--white);
  font-size: 1.875rem;
  margin-right: 0.625rem;
}

:root {
  --font-rubik: "Rubik", sans-serif;
  --font-ruda: "Ruda", sans-serif;
}

.flex {
  display: flex;
  flex-wrap: nowrap;
}

.flex-dir-column {
  flex-direction: column;
}

.flex-dir-row {
  flex-direction: row;
}

.flex-jc-c {
  justify-content: center;
}

.space-around {
  justify-content: space-around;
}

.flex-jc-end {
  justify-content: flex-end;
}

.flex-ai-b {
  align-items: baseline;
}

.flex-ai-c {
  align-items: center;
}

.margin-bottom {
  margin-bottom: 5rem;
}

.flex-jc-sb {
  justify-content: space-between;
}

.wrapper {
  width: 109rem;
  margin: 3.125rem auto;
}

.hero {
  display: block;
}

.section_One {
  margin-top: 18.75rem;
}

.section_Two {
  margin-top: 12.5rem;
}

.last_Section {
  margin-top: 9.375rem;
}

.right {
  position: absolute;
  left: 69.37%;
  top: 171.79%;
}

.mid {
  position: absolute;
  left: 42.3525rem;
  top: 94.695625rem;
}

.left {
  position: absolute;
  left: 7.4375rem;
  top: 91.625rem;
}

.rectangle {
  width: 104.0625rem;
  height: 51.875rem;
  background-color: var(--bg-section);
}

.links {
  margin-top: 1.875rem;
}

.arrow {
  position: absolute;
  left: 87.5rem;
  top: 38.75rem;
  animation: rotation 5s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.vector {
  position: absolute;
  left: 82.8125rem;
  top: 31.25rem;
}

.card {
  margin-top: rem(40);
}

.button {
  position: absolute;
  left: 78.75rem;
  top: 229.6875rem;
}